var randomNumber = require('./random').number;

module.exports = function (mask) {
  var mod = function mod(dividend, divisor) {
    return Math.round(dividend - Math.floor(dividend / divisor) * divisor);
  };

  var n = 8;
  var digits = [];

  for (var i = 0; i < n; i += 1) {
    digits.push(randomNumber(n + 1));
  }

  digits.push.apply(digits, [0, 0, 0, 1]);
  var d1 = digits[11] * 2 + digits[10] * 3 + digits[9] * 4 + digits[8] * 5 + digits[7] * 6 + digits[6] * 7 + digits[5] * 8 + digits[4] * 9 + digits[3] * 2 + digits[2] * 3 + digits[1] * 4 + digits[0] * 5;
  d1 = 11 - mod(d1, 11);
  if (d1 >= 10) d1 = 0;
  var d2 = d1 * 2 + digits[11] * 3 + digits[10] * 4 + digits[9] * 5 + digits[8] * 6 + digits[7] * 7 + digits[6] * 8 + digits[5] * 9 + digits[4] * 2 + digits[3] * 3 + digits[2] * 4 + digits[1] * 5 + digits[0] * 6;
  d2 = 11 - mod(d2, 11);
  if (d2 >= 10) d2 = 0;
  var cnpj = digits.reduce(function (t, curr) {
    return "".concat(t).concat(curr);
  }, '').concat("".concat(d1), "".concat(d2));
  if (mask) return cnpj.replace(/^(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/, '$1.$2.$3/$4-$5');
  return cnpj;
};